@import url('https://fonts.googleapis.com/css?family=Raleway:900|Barlow:300,400,500');

@-webkit-keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

@keyframes fadeIn { 
  0% { opacity: 0; } 
  100% { opacity: 1; }
}

body {
	font-family: 'Barlow', sans-serif;
	font-weight: 900;
	color: #1E1E1E;
	margin: 0;
	padding: 0;
}

.App {
	-webkit-animation: fadeIn 0.5s ease;
	        animation: fadeIn 0.5s ease;
}

.header {
	position: fixed;
	z-index: 20;
	padding: 40px;
}

.header a {
		cursor: pointer;
	}

.landing {
	height: 100vh;
}

/* z-index: -1; */

.landing .fixed {
		position: fixed;
		width: 100%;
		height: 100%;
		z-index: 1;
	}

.about {
	position: absolute;
	bottom: 25%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
}

.about .main .name {
			font-family: 'Raleway', sans-serif;
			display: block;
			font-size: 4.5em;
			text-transform: uppercase;
			line-height: 0.85em
		}

.about .main .name.britti { font-size: calc(4.5em * 0.85);
}

.about .main .links {
			font-weight: 400;
			width: 100%;
			display: block;
			padding-top: 2px;
		}

.about .main .links a {
				color: #2e2e2e;
    		text-decoration: none;
  			border-bottom: 1px #3e3e3e dashed
			}

.about .main .links a:hover { color: #FC4C5D; border-bottom-color: #FC4C5D;
}

.about .blurb {
		font-weight: 300;
		position: absolute;
		width: 200%;
		left: 108%;
		top: 3px;
		font-size: 22px;
		display: block;
	}

.about .blurb p {
			margin: 0;
			letter-spacing: 1px;
		}

@-webkit-keyframes point { 
  0% { top: 50px; } 
  20% { top: 60px; } 
  40% { top: 50px; } 
  100% { top: 50px; } 
}

@keyframes point { 
  0% { top: 50px; } 
  20% { top: 60px; } 
  40% { top: 50px; } 
  100% { top: 50px; } 
}

.scroll-prompt {
	color: #2e2e2e;
	text-decoration: none;
	text-transform: uppercase;
	position: absolute;
	bottom: 10%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	padding: 20px 30px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	-o-user-select: none;
	user-select: none;
	cursor: pointer
}

.scroll-prompt:after { position: absolute; content: ''; -webkit-transform: translate(-50%, -50%) rotate(45deg); -ms-transform: translate(-50%, -50%) rotate(45deg); transform: translate(-50%, -50%) rotate(45deg); border-bottom: 2px #1e1e1e solid; border-right: 2px #1e1e1e solid; width: 10px; height: 10px; left: 50%; -webkit-animation: point 2.3s infinite cubic-bezier(.51,.02,.54,1); animation: point 2.3s infinite cubic-bezier(.51,.02,.54,1);
}

.MainLinks {
	font-family: 'Raleway', sans-serif;
	top: 40%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%) rotate(-90deg);
	    -ms-transform: translate(-50%,-50%) rotate(-90deg);
	        transform: translate(-50%,-50%) rotate(-90deg);
}
.works {
	height: 100vh;
	position: relative;
	background: #fff;
	z-index: 12;
	width: 100%;
}
.works .shadow {
		position: absolute;
		z-index: 11;
		top: -60px;
    height: 60px;
		left: 0;
		width: 100%;
		background: -webkit-gradient(linear,left top, left bottom,from(transparent),to(rgba(102, 102, 102, 0.13333)));
		background: -webkit-linear-gradient(top,transparent,rgba(102, 102, 102, 0.13333));
		background: -o-linear-gradient(top,transparent,rgba(102, 102, 102, 0.13333));
		background: linear-gradient(180deg,transparent,rgba(102, 102, 102, 0.13333));
		pointer-events: none;
	}

@media only screen and (max-width: 1000px)  {
		.App .about {
			left: 12%;
			-webkit-transform: none;
			    -ms-transform: none;
			        transform: none;
		}
			.App .about .blurb {
				top: auto;
				left: 0;
				bottom: -95%;
			}
		.App .scroll-prompt {
			bottom: 20%;
			right: 10%;
			left: auto;
		}
}

@media only screen and (max-width: 600px)  {
		.App .about {
			left: 12%;
			bottom: 20%;
			-webkit-transform: none;
			    -ms-transform: none;
			        transform: none;
		}
				.App .about .name span {
					font-size: 3.5em
				}
				.App .about .name span.britti { font-size: calc(3.5em * 0.85);
  }
			.App .about .blurb {
				top: auto;
				font-size: 17px;
				left: 0;
				bottom: -70%;
			}
		.App .scroll-prompt {
			display: none;
		}
		.App .MainLinks {
			top: calc(50% - 7em);
		}
}


