@-webkit-keyframes slideUp { 
  0% { left: -40px; } 
  100% { left: 0; }
}

@keyframes slideUp { 
  0% { left: -40px; } 
  100% { left: 0; }
}

@-webkit-keyframes slideDown { 
  0% { left: 40px; } 
  100% { left: 0; }
}

@keyframes slideDown { 
  0% { left: 40px; } 
  100% { left: 0; }
}

@-webkit-keyframes scaleIn { 
  0% { -webkit-transform: scale(0.8); transform: scale(0.8); } 
  100% { -webkit-transform: none; transform: none; }
}

@keyframes scaleIn { 
  0% { -webkit-transform: scale(0.8); transform: scale(0.8); } 
  100% { -webkit-transform: none; transform: none; }
}

.MainLinks {
	position: absolute;
	text-align: center;
	-webkit-perspective: 45px;
	        perspective: 45px;
}

.MainLinks .link {
		text-transform: uppercase;
		z-index: 1;
		position: relative;
		font-size: 7em;
		line-height: 0.9em;
		/* cursor: pointer; */
		display: inline-block;
		-webkit-transition: -webkit-transform 0.25s cubic-bezier(.12,.26,.16,.9);
		transition: -webkit-transform 0.25s cubic-bezier(.12,.26,.16,.9);
		-o-transition: transform 0.25s cubic-bezier(.12,.26,.16,.9);
		transition: transform 0.25s cubic-bezier(.12,.26,.16,.9);
		transition: transform 0.25s cubic-bezier(.12,.26,.16,.9), -webkit-transform 0.25s cubic-bezier(.12,.26,.16,.9)
		/* &:after {
			content: '';
			position: absolute;
			width: 0px;
			height: 4px;
			border-radius: 5px;
			background: #1e1e1e;
			left: -30px;
			top: calc(50% - 2px);
			transition: width 0.25s ease;
		}
		&:before {
			content: '';
			position: absolute;
			width: 0px;
			height: 4px;
			border-radius: 5px;
			background: #1e1e1e;
			right: -30px;
			top: calc(50% - 2px);
			transition: width 0.25s ease;
		} */
		/* &:hover {
			transform: scale(1.1);
			&:before {
				width: 20px;
			}
			&:after {
				width: 20px;
			}
		} */
	}

.MainLinks .link.front { font-size: 7.2em; z-index: 3; line-height: 0.8em; -webkit-animation: 0.65s cubic-bezier(.25,.46,.26,.98) scaleIn; animation: 0.65s cubic-bezier(.25,.46,.26,.98) scaleIn;
}

.MainLinks .link.top { -webkit-animation: 0.65s cubic-bezier(.25,.46,.26,.98) slideUp; animation: 0.65s cubic-bezier(.25,.46,.26,.98) slideUp;
  /* &:hover {
				transform: scale(1.07) translateY(-3px);
			} */
}

.MainLinks .link.bottom { -webkit-animation: 0.65s cubic-bezier(.25,.46,.26,.98) slideDown; animation: 0.65s cubic-bezier(.25,.46,.26,.98) slideDown;
  /* &:hover {
				transform: scale(1.07) translateY(3px);
		 } */
}

.MainLinks .link-plane {
		width: 100%;
		height: 0;
		-webkit-perspective-origin: center;
		        perspective-origin: center;
		position: relative
	}

.MainLinks .link-plane:after { content: ''; width: 35%; height: 24px; position: absolute; top: 50%; left: 50%;
}

.MainLinks .link-plane.middle::after {
		width: 40%;
	}

.MainLinks .teal::after {
		background: #5FF2D6;
		-webkit-transform: translate(-50%, -20%) rotateX(-60deg);
		        transform: translate(-50%, -20%) rotateX(-60deg);
		z-index: 0;
		height: 20px;
	}

.MainLinks .blue {
		margin-top: 7px
	}

.MainLinks .blue:after { background: #4040CC; -webkit-transform: translate(-50%, -50%) rotateX(-60deg); transform: translate(-50%, -50%) rotateX(-60deg); z-index: 2;
}

.MainLinks .yellow {
		margin-bottom: 7px
	}

.MainLinks .yellow:after { background: #FCDA69; -webkit-transform: translate(-50%, -50%) rotateX(60deg); transform: translate(-50%, -50%) rotateX(60deg); z-index: 2;
}

.MainLinks .red::after {
		background: #FC4C5D;
		z-index: 0;
		-webkit-transform: translate(-50%, -80%) rotateX(60deg);
		        transform: translate(-50%, -80%) rotateX(60deg);
		height: 20px;
	}

@media only screen and (max-width: 600px)  {
		.MainLinks .links {
			top: 45%;
		}
		.MainLinks .link {
			font-size: 4em
		}
		.MainLinks .link.front { font-size: 4.3em;
  }
}


